// 
// accordions.scss
//

.custom-accordion {
    .accordion-arrow {
        font-size: 1.2rem;
        position: absolute;
        right: 0;
    }

    a {
        &.collapsed {
            i.accordion-arrow {
                &:before {
                    content: "\F0142";
                }
            }
        }
    }
}

.accordion-header {
    margin: 0;

}

.accordion-button {
    font-weight: $font-weight-medium;
    color: $dark;

    &:focus {
        box-shadow: none;
    }
}